.section > main {
  width: 1200px;
  margin: -40px auto 0;
  position: relative;
  clear: both; }

.section h2 {
  font-family: "FranklinGothic-ExtraCond", serif;
  font-size: 50px;
  color: #000;
  background: url(../../assets/images/sprite_main.png) no-repeat 0 -197px;
  width: 470px;
  height: 91px;
  line-height: 100px;
  text-align: center;
  text-transform: uppercase;
  margin: 0 auto;
  position: relative;
  top: -45px;
  z-index: 5; }



